      </div>
      </div>
        

	</div>
</template>

<script>
import BScroll from 'better-scroll';
    import {mapGetters} from 'vuex';
    import star from '../star/star.vue';
    export default{
        components:{
            star
        },
         computed:{
            ...mapGetters([
                'seller',
                'detailShow'
            ])
        },
		created(){
			this.$store.dispatch('getSeller');
		
		},
		watch:{
			seller(){
				this.$nextTick( () => {
                    this.menuBS();
				});
			}	
		},
		methods:{
			menuBS(){
                let menuBScroll = new BScroll( this.$refs.cc , {} );
			},
		}
	}

    
</script>

<style scoped lang="less">

.seller{
   height: 9rem;
}
    .title{
        margin: 0.36rem;
        border-bottom: 1px solid gainsboro;
        span{
