            border-top: 1px solid gainsboro;
            height: 0.8rem;
            font-size: 0.24rem;
            color: rgb(7, 17, 27);
            margin-top: 0.24rem;
            font-weight: 200;
            line-height: 1.1rem;
            
        }
        .favourable img{
            height: 0.4rem;
            width: 0.4rem;
            vertical-align:middle;
            margin-bottom: 0.055rem;
            margin-right: 0.12rem;
            margin-left: 0.24rem;
        }

</style>



