<template>
	<div class="ratings" ref="cc">
		<div>
			<div class="score">
				<div class="general-score">
					<p class="score-num">{{seller.score}}</p>
					<p class="score-name">综合评分</p>
					<p class="score-higher">高于周边商家69.2%</p>
				</div>
				<div class="vertical-line"></div>
				<div class="score-tree">
					<div class="attitude">
						<div class="attitude-line1">
							<span class="service-attitude">服务态度</span>
							<star :score='seller.score' :size="'small'" class="star"></star>
						</div>
						<div class="attitude-line2">
							<span class="service-attitude">服务态度</span>
							<star :score='seller.score' :size="'small'" class="star"></star>
						</div>
						<div class="time">
							<span class="service-attitude">送达时间</span>
							<span class="forty-four">44分钟</span>
						</div>
					</div>
				</div>
			</div>
			<div class="widthhr"></div>
			<div class="comment">
				<div class="btn-all">
					<input type="button" value="全部" class="btn btn-primary btn1">
					<input type="button" value="推荐" class="btn btn-primary btn2">
					<input type="button" value="吐槽" class="btn btn-primary btn3">
				</div>
				<hr class="line">
				<div class="comment-item">
					<div class="item-line1">
						<div>
							<img src="" alt="">
						</div>
						<div>
							<span>username</span>
							<star :score='seller.score' :size="'small'" class="star"></star>
						</div>
						
						<span>time</span>
					</div>
					<div>
						{{seller}}
					</div>
				</div>
			</div>
			<div class="aa">
				<p>{{seller.name}}</p>
			</div>
		</div>
	</div>
</template>

<script>
	import { mapGetters } from "vuex";
	import BScroll from 'better-scroll';
	import star from '../../components/star/star.vue';

	export default{
		props: ["name"],
		components: {
            star
		},
		watch: {
			seller(){
				this.$nextTick(() => {
					this._menuBS();
				})
			}
		},
		computed: {
            ...mapGetters([
                "seller"
            ])
		},
		methods: {
            _menuBS(){
                let menuBScroll = new BScroll(this.$refs.cc, {});
            }
        }
	}
</script>
<style lang="less" scoped>
	.ratings{
		width: 99%;
		height: 10rem;
		.score{
			width: 110%;
			height: 2.15rem;
			border-top: 1px solid rgba(7,17,27,0.1);
			display: flex;
			padding: .36rem 0 .48rem 0;
			.general-score{
				width: 2.75rem;
				text-align: center;
				.score-num{
					font-size: .48rem;
					color: rgb(255,153,0);
					line-height: .56rem;
					margin-bottom: .12rem;
				}
				.score-name{
					font-size: .24rem;
					color: rgb(7,17,27);
					margin-bottom: .16rem;
				}
				.score-higher{
					font-size: .2rem;
					color: rgb(7,17,27);
					line-height: .2rem;
				}
			}
			.vertical-line{
				width: .02rem;
				height: 1.5rem;
				background-color: rgba(7,17,27,0.1);
			}
			.score-tree{
				width: 4.74rem;
				padding: 0 .48rem;
				.attitude{
					.attitude-line1{
						display: flex;
						margin: 0 0 .16rem 0;
						.service-attitude{
							font-size: .24rem;
							color: rgb(7,17,27);
							line-height: .36rem;
						}
					}
					.attitude-line2{
						display: flex;
						margin: 0 0 .16rem 0;
						.service-attitude{
							font-size: .24rem;
							color: rgb(7,17,27);
							line-height: .36rem;
						}
					}
					.time{
						.service-attitude{
							font-size: .24rem;
							color: rgb(7,17,27);
							line-height: .36rem;
						}
						.forty-four{
							font-size: .28rem;
							font-weight: 500;
							margin-left: 0.2rem;
							color: rgb(147,153,159);
						}
					}
					.star{
						width: 2.5rem;
						height: 0.5rem;
						margin-left: 0.2rem;
					}
				}
			}
		}
	.widthhr{
		border-top: 1px solid grey;
		border-bottom: 1px solid grey;
		opacity: 0.2;
		width: 10rem;
		height: 0.32rem;
		background: rgba(147, 153, 159,0.5);
	}
		.comment{
			padding: .36rem;
			.btn-all{
				margin: 0 0 .36rem 0;
                .btn1{
                    width: 1.2rem;
                    height: 0.63rem;
                    border-radius: .24rem;
                    background-color: rgb(0,160,220);
                    border: 0;
                }
                .btn2{
                    width: 1.2rem;
                    height: 0.63rem;
                    border-radius: .24rem;
                    background-color: rgba(0,160,220,0.2);
                    border: 0;
                }
                .btn3{
                    width: 1.2rem;
                    height: 0.63rem;
                    border-radius: .24rem;
                    background-color: rgba(77,85,93,0.2);
                    border: 0;
                }
			}
			.line{
				width: 6.6rem;
			}
			.comment-item{
				.item-line1{
					width: 100%;
					height: .6rem;
					border: 1px solid red;
					display: flex;

				}
				
			}
		}
		.aa{
			
			height: 5rem;
			padding: 100rem;
		}
	}
</style>



