import types from '../types.js';
import axios from 'axios';

var state = {

	goods : [],
	food : {},
	foodshow : false

}

var getters = {

	goods( state ){
		return state.goods;
	},
	food( state ){
		return state.food;
	},
	foodshow(state){
		return state.foodshow;
    }
}

var actions = {

	getGoods( { commit,state } ){
		axios.get('./data.json').then( (res) => {
		
			// if( res.data.errno === 0 ){
				commit( types.GET_GOODS , res.data.goods );
			// }
		})
	},
	showFood( {commit,state} , f ){
		commit( 'cc' , f );
	},
	close({commit}){
		commit('close');
	}
}

var mutations = { 

	[types.GET_GOODS]( state , data ){
		state.goods = data;
	},
	cc(state,f){
		state.food = f;
		state.foodshow = true;
	},
	close(state){
		state.foodshow = false;
	}
	
}

export default {
	state,
	getters,
	actions,
	mutations
}


