<template>
  <div class="goods" >
		<div class="menu-wrap" ref="cc">
            <div>
                <div class="menu-item"  v-for='(good,i) in goods' :key="i" @click='selectMenu(i)' :class = "{'current' : i === currentIndex}">
                    <div>{{good.name}}</div>
                </div>
            </div>
        </div>    
    <div class="foods-wrap" ref="bb">
        <div>
            <div  v-for='(good,i) in goods' :key="i" class="food-list-hook">
                <div class="tetile">{{good.name}}</div>
            <div class="main" v-for='(food,i) in good.foods' :key="i">
            <img :src="food.image" alt=""  @click = 'showFood(food)'>
            <div class="main-left">
                <div class="maintop">{{food.name}}</div>
                <div class="aa">{{food.description}}</div>
                <div class="maincenter">
                    <span>月销{{food.sellCount}}份</span>
                    <span>好评率{{food.rating}}％</span>
                </div>
          
            </div>
                  <div class="mainbottom">
                ¥{{food.price}}
                </div>
                <div class="cart">
					<cartControl :food = 'food'></cartControl>
			</div>
        </div> 
    </div> 
        </div>

</div>
<foodDetail></foodDetail>
<v-foot  :goods = 'goods'></v-foot>
</div>
</template>

<script>
    import foot from '../../components/foot/foot.vue';
	import BScroll from 'better-scroll';
    import cartControl from '../cartControl/cartcontrol.vue';
    import foodDetail from '../foodDetail/foodDetail.vue';
	import { mapGetters } from 'vuex';
	export default {

        	data(){
			return {
				listHeight : [],
				scrollY:0
			}
		},
        components:{
            foodDetail,
            cartControl,
            'v-foot' : foot,
         },
		created(){
			this.$store.dispatch('getGoods');
		
		},
		watch:{
			goods(){
				this.$nextTick( () => {
                    this.menuBS();
                    this.calcHeight();
				});
			}	
		},
		computed:{
			...mapGetters([
				'goods'
            ]),
            	currentIndex(){
				for( let i = 0 ; i < this.listHeight.length ; i++ ){
					let height1 = this.listHeight[i];		
					let height2 = this.listHeight[i+1];		
					if( this.scrollY >= height1 && this.scrollY  < height2 ){
						return i;
					}
				}
				return 0 ;
			}
		},
		methods:{
			menuBS(){
                let menuBScroll = new BScroll( this.$refs.cc , {
                      click:true
                } );
              
              		this.foodBScroll = new BScroll( this.$refs.bb , {
					click : true,
					probeType:3
                } );
                	this.foodBScroll.on('scroll' , (pos) => {
					console.log( Math.abs( Math.ceil(pos.y)));
					this.scrollY =  Math.abs(Math.round(pos.y)) + 2;
				})
            },
            	calcHeight(){
				let foodList = this.$refs.bb.getElementsByClassName('food-list-hook');
				let height = 0;
				this.listHeight.push( height );
				for( let i = 0 ; i < foodList.length ; i++){
					height +=  foodList[i].clientHeight;
					this.listHeight.push( height );
				}
				console.log( this.listHeight );
			},
          	selectMenu(i){
				console.log(i);
				let foodList = this.$refs.bb.getElementsByClassName('food-list-hook');
				let el = foodList[i];
				this.foodBScroll.scrollToElement( el , 200 );
			},
	        showFood(food){
				this.$store.dispatch('showFood',food);
			}
            
		}
	}

</script>


<style scoped lang="less">
    .goods{
        position: absolute;
        display: flex;
        overflow: hidden;
        width: 100%;
        top:3.5rem;
        bottom: 0.9rem;
        .menu-wrap{
            flex:0 0 1.6rem;
            width: 1.6rem;
            background:#F3F5F7;
         	.menu-item{
				width: 1.12rem;
				height: 1.08rem;
				padding: 0 0.24rem;
                display: table;
                &.current{
                    color:red;
                    height: 1.2rem;
					background: white;
				}
				div{
					display: table-cell;
					font-size: 0.24rem;
					font-weight: 200;
					line-height: 0.28rem;
					vertical-align: middle;
					border-bottom: 1px solid rgba(7,17,27,0.1);
				}
			}
            }
        }
        .foods-wrap{
            flex: 1;
        }
        .aa{
            margin-top: 0.1rem;
            height: 0.2rem;
            overflow: hidden;
      
        }
        .tetile{
            line-height: 0.52rem;
            height: 0.52rem;
            padding-left: 0.28rem;
            font-size: 0.24rem;
            background-color: #f3f5f7;
            color: rgb(147, 153, 159);
            border-left: 2px solid #d9dde1;
        }
        .main{
            width: 6rem;
            margin-left: 0.36rem;
            margin-top: 0.36rem;
            height: 1.685rem; 
            border-bottom: 1px solid rgba(7,17,27,0.1);
            position: relative;
            img{
                width: 1.14rem;
                height: 1.14rem;
            }
            	.cart{
						position: absolute;
						right: 0.3rem;
						bottom:0.2rem;
					}
        }
        .main-left{
            display: inline-block;
            width: 70%;
            margin-right: 0.36rem;
            float: right;
        }
        .maintop{
           margin-top: 0.04rem;
           font-size: 0.28rem;
           color: rgb(7, 17,27 );
           line-height: 0.28rem;

        }
        .maincenter{
            margin-top: 0.16rem;
            font-size: 0.2rem;
            color: rgb(147, 153, 159);
            span{
                margin-right: 0.24rem;
                
            }
        }
        .mainbottom{
            margin-bottom: 0.2rem;
            font-size: 0.28rem;
            color: red;
            width: 65%;
            position: relative;
            left: 1.4rem;
            top:-0.1rem;
            font-weight: 700;
            line-height: 0.48rem;
            
            img{
                width: 0.48rem;
                height: 0.48rem;
                float: right;
                margin-right: 0.15rem;
            }
            span{
                color: gray;
                float: right;
                margin-right: 0.15rem;
            }
        }
</style>


