<template>
	<transition enter-active-class="animated bounceInLeft" leave-active-class="animated bounceOutRight">
		<div class="detail" v-show = 'detailShow'>
			
		<p class="maintop" @click='hideDetail'>{{seller.name}}</p>
		<star class="star" :score = 'seller.score' :size = "'big'"></star>
        <div class="maincenter">
            <div class="hrleft"></div>
            <span>优惠信息</span>
            <div class="hrright"></div>
        </div>
        <div class="mainfavourable">
        <div class="favourable" v-if='seller.supports'>
            <img  src="../../assets/imgs/decrease.png" alt="">
                {{seller.supports[0].description}}
        </div>
        <div class="favourable" v-if='seller.supports'>
            <img  src="../../assets/imgs/discount.png" alt="">
                {{seller.supports[2].description}}
        </div>
        <div class="favourable" v-if='seller.supports'>
            <img  src="../../assets/imgs/discount.png" alt="">
                {{seller.supports[1].description}}
        </div>
        <div class="favourable" v-if='seller.supports'>
            <img  src="../../assets/imgs/special.png" alt="">
                {{seller.supports[2].description}}
        </div>
        <div class="favourable" v-if='seller.supports'>
            <img  src="../../assets/imgs/special.png" alt="">
                {{seller.supports[1].description}}
        </div>
        </div>
        <div class="maincenter">
            <div class="hrleft"></div>
            <span>商家公告</span>
            <div class="hrright"></div>
        </div>
        <div class="text">
             {{seller.bulletin}}
        </div>
        <div class="mainbottom">
            <img src="../../assets/imgs/4.png" alt="">
        </div>
	</div>
	</transition>

</template>

<script>
    import {mapGetters} from 'vuex';
    import animate from 'animate.css';
    import star from '../star/star.vue';

    export default {
        props:['name'],
        components:{
            star
        },
        computed:{
            ...mapGetters([
                'seller',
                'detailShow'
            ])
        },
        methods:{
            hideDetail(){
                this.$store.dispatch('hideDetail')
            }
        }
    }
</script>

<style lang="less" scoped>
    .detail{
        font-size: 0.4rem;
        padding-left: 0.72rem;
        padding-right: 0.68rem;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
        background:rgba(7,17,27,0.8);
        }
        .star{
            margin-top: 0.32rem;
        text-align: center;   
        font-size: 0.4rem;
         margin-left: 0.5rem;
        }
        .maintop{
            margin-top:1.28rem;
            text-align: center;
            font-size: 0.32rem;
            font-weight: 700;
            color: rgb(255, 255, 255);
        }
        .maincenter{
            font-size: 0.3rem;
            margin-top: 0.56rem;
            color:  rgb(255, 255, 255);
        }
        .hrleft {
            margin-right: 0.24rem;
            margin-top: 0.15rem;
            width: 2.24rem;
            border-top: 2px solid grey;
            float: left;
            opacity: 0.5;
        }
        .hrright {
            margin-left: 0.24rem;
            margin-top: -0.15rem;
            width: 2.24rem;
            border-top: 2px solid grey;
            float: right;
             opacity: 0.5;
        }
        .mainfavourable{
            margin-top: 0.48rem;
            margin-left: 0.24rem;
        }
        .favourable{
            font-size: 0.24rem;
            color: rgb(255, 255, 255);
            margin-top: 0.24rem;
            font-weight: 200;
        }
        .favourable img{
            height: 0.32rem;
            width: 0.32rem;
            vertical-align:middle;
            margin-bottom: 0.055rem;
            margin-right: 0.12rem;
        }
        .text{
            font-size: 0.24rem;
            font-weight: 200;
            color: rgb(255, 255, 255);
            line-height: 0.48rem;
            margin: 0.48rem 0.24rem 0 .24rem;
        }
        .mainbottom{
            text-align: center;
            margin-top: 1.6rem;
            img{
                width: 0.4rem;
                height: 0.4rem;
            }
        }
</style>




