<template>
  <div class="foot">
      <div class="foot1">

      
      <div class="img">
          	<div class="left" :class = "{'cc' : totalCount > 0 }">{{totalCount}}</div>
          <img  class="img1" src="../../assets/imgs/购物车 (2).png" alt="">
        
      </div>
        <div class="div1 one">¥{{totalPrice}}</div>
          <div class="div1 two">另需配送费¥4元</div>
      </div>
      <div class="footright"> 
         {{pay}}
      </div>
      
  </div>
</template>

<script>
   	import { mapGetters } from 'vuex';
    export default {
    props:['goods'],
    	computed : {
			...mapGetters([
				'seller'
            ]),
            selcetFoods(){
			let  foods = [];
			this.goods.forEach( good => {
				good.foods.forEach( food => {
					if( food.count > 0 ){
						foods.push( food );
					}
				})
			});
			return foods;
		},
  	totalCount(){
				let count = 0 ;
				this.selcetFoods.forEach( food => {
					count += food.count;
				});
				return count;
			},
	
			totalPrice(){
				let price = 0;
				this.selcetFoods.forEach( food => {
					price += food.count * food.price;
				});
				return price;
			},
		
			pay(){

				if( this.totalPrice === 0 ){
					return `¥ ${this.seller.minPrice} 元起送`;
				}else if ( this.totalPrice < this.seller.minPrice ) {
					return `还差¥ ${this.seller.minPrice - this.totalPrice } 元起送`
				}else{
					return "去结算";
				}
			}
		}
	}
</script>

<style scoped lang='less'>
    .foot {
        width: 100%;
        height: 1rem;
        background-color:#141d27;
        bottom: 0;
        position: fixed;
        z-index: 9;
    }
    .img{
        margin-left: 0.36rem;
        width: 0.88rem;
        height: 0.88rem;
        background: #cccccc;
        border-radius: 50%;
        margin-top: 0.05rem;
        position: relative;
        top: -0.3rem;
        
    }
    // .left{
    //     &cc{
    //          width: 0.4rem;
    //     height: 0.3rem;
    //     color: white;
    //     text-align: center;
    //     line-height: 0.3rem;
    //     border-radius: 40%;
    //     background: red;
    //     position:absolute;
    //     left:0.6rem; 
    //     }
      
    // }
    	.left{
			display: none;
			&.cc{
display: block;
        width: 0.4rem;
        height: 0.3rem;
        color: white;
        text-align: center;
        line-height: 0.3rem;
        border-radius: 40%;
        background: red;
        position:absolute;
        left:0.6rem; 
			}
		}
    .img1{
         width: 0.5rem;
         height: 0.5rem;
        position: relative;
        top: 0.2rem;
        left: 0.15rem;
        }
        .footright{
            position: absolute;
           background: rgba(255, 255, 255 ,0.1);
          
           height: 1rem;
           width: 2.1rem;
           left: 5.4rem;
           top: 0.01rem;
           color: gray;
           line-height: 1rem;
           text-align: center;
           font-weight: 700;
           font-size: 0.24rem;
        }
  
        .div1{
           position: relative;
           top: -0.5rem;
           left: 1.5rem;
           color: rgba(255, 255, 255,0.4)
        }
        .one{
            font-size: 0.32rem;
            font-weight: 700;
            position: relative;
            top: -0.55rem;
            width: 0.5rem;        

        }
        .two{
            float: left;
            position: relative;
            left: 2.14rem;
            padding-left: 0.24rem;
            top: -0.95rem;
            height: 0.5rem;
            line-height: 0.5rem;        
          border-left: 1px solid gray;
            font-size: 0.24rem;
           
        }
     
</style>




