<template>
  <div class="header">
      <div class="bg">
          <img :src="seller.avatar" class="bgimg" alt="">
      </div>
      <div class="main">
        
        <img src="../../assets/imgs/bg.png" alt="" @click = 'showDetail'>
        <div class="main-left">
            <div class="maintop">
                <span>&nbsp;品牌&nbsp;</span>  
                粥品香坊（大运村）
            </div>
            <div class="maincenter">{{seller.description}}/38分钟送达</div>
            <div class="mainbottom" v-if='seller.supports'>
                <img  src="../../assets/imgs/decrease.png" alt="">
       
        {{seller.supports[0].description}}
                </div>
        </div>
        <div class="mainright">
            <span>
                 五个&nbsp;>
            </span>
           
        </div>

      </div>
        <div class="main2">
            <span class="span">&nbsp;公告&nbsp;</span>
            <span class="span1">{{seller.bulletin}}</span>
        </div>
  </div>
</template>

<script>
    import { mapGetters,mapActions} from 'vuex';
    export default {
        computed:{
            ...mapGetters([
                'seller'
            ])
        },
        	methods:{
			...mapActions([
				'showDetail'
			])
		}
    }
</script>

<style scoped lang='less'>
    .header {
        width: 100%;
        height: 2.68rem;
        background-color: rgba(7,17,27,0.5);
        position: relative;
        overflow: hidden;
        p{
            color: red;
            font-size: 0.3rem;
        }
        img{
            width: 1.28rem;
            height: 1.28rem;
            border-radius:0.04rem;
        
        }
    }
    .bg{
     position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
        filter: blur(0.05rem);
        img{
            width: 100%;
            height: 100%;
        }
        
    }
    .main{
        height: 1.65rem;
        padding-top: 0.48rem;
        padding-left: 0.48rem;
    }
    .main-left{
      
        height: 1.28rem;
        width: 75%;
        margin-right: 0.16rem;
        float: right;
    }
    .mainright{
        position: absolute;
        top: 1.3rem;
        left: 6.2rem;
        height: 0.48rem;
        width: 1rem;
        border-radius: 0.4rem;
        background-color: rgba(7,17,27,0.2);
        text-align: center;
        line-height: 0.48rem;
        


    }
    .mainright span{
        color: white;
    }
    .main2{
        height: 0.56rem;
        padding-left: 0.24rem;
        background-color: rgba(7,17,27,0.2);
    }
    .span{
        background-color: white;
        line-height: 0.56rem;
        font-size: 0.12rem;
        border-radius: 0.04rem;
        font-weight: bold;
        
    }
    .span1{
        margin-left: 0.08rem;
        line-height: 0.56rem;
        font-size: 0.2rem;
        color: white;
    }
    .maintop{
        font-size: 0.32rem;
        color: rgb(255,255,255);
        font-weight: bold;
        line-height: 0.36rem;
    }
    .maintop span{
        background-color: red;
        font-weight: bold;
        height: 0.32rem;
        font-size: 0.27rem;
    }
    .maincenter{
        font-size: 0.24rem;
        color: rgb(255, 255, 255);
        font-weight: 200;
        line-height: 0.24rem;
        margin-top: 0.18rem;
    }
    .mainbottom{
        font-size: 0.2rem;
        color: rgb(255, 255, 255);
        font-weight: 200;
        margin-top: 0.18rem;
   
    }
    .mainbottom img{
        height: 0.3rem;
        width: 0.3rem;
        vertical-align:middle;
        margin-bottom: 0.055rem;
    }
  

</style>




