<template>
	<div class="cart">
		<div class="add"  @click = 'reduce' >
             <img v-show='food.count'src="../../assets/imgs/减号2.png" alt="">
        </div>

		<div class="count" v-if="food.count">{{food.count}}</div>

		<div class="reduce"@click = 'add' >
             <img    src="../../assets/imgs/加号 (1).png" alt="">
        </div>
	</div>
</template>

<script>
	import Vue from 'vue';

	export default {
		props:['food'],
		methods:{
			add(){
				clearInterval(this.timer);
				this.timer = setTimeout(()=>{
					if(!this.food.count){
						Vue.set(this.food,'count',1);
					}else{
						this.food.count ++;
					}
				},0)
			},
			reduce(){
				clearInterval(this.timer);
				this.timer = setTimeout(()=>{
					if(!this.food.count){
						Vue.set(this.food,'count',1);
					}else{
						this.food.count --;
					}
				},0)
				
			}
		}
	}
</script>

<style lang="less" scoped>
		
	.cart{
		
		height: 0.4rem;
	
		display: flex;
		.reduce{
			flex:1;
			img{
                width: 0.5rem;
                height: 0.5rem;
            }
		}
		.count{
			flex:1;
            height: 0.5rem;
            width: 0.4rem;
            text-align: center;
            line-height: 0.5rem;
            font-size: 0.3rem;
		}
		.add{
			flex:1;
			img{
                margin-top: 0.02rem;
                width: 0.47rem;
                height: 0.47rem;
            }
		}
	}

</style>


