<template>
  <div class="food" v-show="foodshow" ref="cc">
	  <div>
      <div class="imgshow">
          <img :src="food.image" alt=""  @click='close'>
	
      </div>
	  	  <div class="maintop">
			  <p >{{food.name}}</p>
			  <div class="main1">
				  <span>月销{{food.sellCount}}份</span>
				  <span>好评率{{food.rating}}％</span>
			  </div>
			  <div class="main2">
				  <span>¥{{food.price}}</span>
				  	<div class="cart">
						<div class="cc" v-show='!food.count' @click = 'addFirst'>加入购物车</div>
						<cartControl :food = 'food' v-show='food.count'></cartControl>
					</div>
			  </div>
		  </div>     
		  <div class="widthhr"></div>
		  <div class="maintext">
			  <p>商品介绍</p>
			  <p class="maintext1">{{food.info}}</p>
		  </div>
		  <div class="widthhr"></div>
		  <div class="mainbottom">
				  <p>商家评价</p>
					<div class="button1 one">全部</div>
					<div class="button1 two">推荐</div>
					<div class="button1 three">吐槽</div>
			  <hr class="hr">
		  </div>
	</div>
  </div>
</template>

<script>
	import BScroll from 'better-scroll';
	import cartControl from '../cartControl/cartcontrol.vue';
	import{mapGetters} from 'vuex';
	import Vue from 'vue';
    export default {
		
        computed:{
            ...mapGetters([
                'foodshow',
                'food'
			]),
			
		},
		components:{
			cartControl
		},
		created(){
			this.$store.dispatch('getGoods');
		
		},
		watch:{
			food(){
				this.$nextTick( () => {
                    this.menuBS();
				});
			}	
		},
		methods:{
			menuBS(){
                let menuBScroll = new BScroll( this.$refs.cc , {
					click : true,
				} );
			},


			close(){
			
				console.log(111)
				this.$store.dispatch('close');
			},
			addFirst(){
				console.log(111)
				Vue.set( this.food , 'count' , 1 );
		
		}
		}
	}
</script>

<style lang="less" scoped>
	.food {
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: white;
		.imgshow{
			position: relative;
			width: 100%;
			height: 0;
			padding-top: 100%;
			img{
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
			}
		}
	}
	.maintop{
		padding: 0.36rem;
		
		p{
			font-size: 0.28rem;
			font-weight: 700;
			color: rgb(7,17,27);
			line-height: 0.28rem;
		}
	}
	.main1{
		margin-top: 0.16rem;
		font-size: 0.2rem;
		color: rgb(147, 153, 159);
		margin-right: 0.24rem;
	}
	.main2{
		margin-top: 0.36rem;
		font-size:0.28rem;
		font-weight: 700;
		color: rgb(240, 20, 20);
		line-height: 0.48rem; 
		margin-bottom: 0.36rem;

	}
	.widthhr{
		border-top: 1px solid grey;
		border-bottom: 1px solid grey;
		opacity: 0.2;
		height: 0.32rem;
		background: rgba(147, 153, 159,0.5);
	}
	.maintext{
	padding: 0.36rem;
		p{
			font-size: 0.28rem;
			color: rgb(7,17,27);
		}
		.maintext1{
			margin-left: 0.16rem;
			font-size: 0.24rem;
			font-weight: 200;
			color: rgb(77, 85, 93);
			line-height: 0.48rem;
		}
	}
	.mainbottom{
		
		padding: 0.36rem;
		p{
			font-size: 0.28rem;
			color: rgb(7,17,27);
		}
	}
	.cc{
		position: absolute;
		top: 4.5rem;
		width: 1.5rem;
		left: 2.8rem;
	}
	.cart{
		position: absolute;
		top: 4.5rem;
		left: 3rem;
	}

	.button1{
				font-size: 0.24rem;
				margin-top: 0.24rem;
				color: rgb(255, 255, 255);
				border-radius: 0.05rem;
				background-color:rgb(0, 160, 220);
				width: 1.2rem;
				height: 0.6rem;		
				text-align: center;
				line-height: 0.6rem;
				display: inline-block;
				margin-right: 0.16rem;
				margin-bottom: 0.36rem;
			}
			.two{
				background: skyblue;
			}
			.three{
				background: #cccccc;
			}
			.hr{
				opacity: 0.2;
			}
</style>




